.ct-instance-selector {
	display: flex;
}

.ct-conditions-trigger {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 55px;
	cursor: pointer;
	margin-inline-start: 5px;
	background: var(--accentColor);
	border-radius: 3px;
	transition: background 0.2s cubic-bezier(0.455, 0.03, 0.515, 0.955);

	&:before {
		font-family: 'dashicons';
		content: "\f503";
		font-size: 16px;
		color: #fff;
	}

	&:hover {
		background: var(--accentColorHover);

		.ct-tooltip-top {
			opacity: 1;
			visibility: visible;
			transform: translate3d(0px, -38px, 0px);
		}
	}
}

// modal
.ct-conditions-modal {

	.ct-control {
		text-align: left;
		margin-bottom: 20px;
	}
}

.ct-conditions-actions {
	margin-top: auto;
	padding-top: 20px;
	border-top: 1px dashed #eee;

	.button-primary {
		width: 100%;
		min-height: 42px;
		font-size: 14px;
		font-weight: 500;
	}
}